<template>
	<div id="mapContainer">
		<MapButtons
			v-on:locate="locateUser"
			v-on:save="save"
			v-on:edit="edit"
			v-on:create="create"
			v-on:savePlekje="savePlekje"
			v-on:cancelSelectPlekje="cancelSelectPlekje"
            v-on:saveHere="saveHere"
			:focusPoint="focusPoint"
            :userLocation="userLocation"
		></MapButtons>
	</div>
</template>

<script>
var api = require("@/utils/api");
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-draw";
import "leaflet-draw/dist/leaflet.draw.css";
var map;

import MapButtons from "../components/MapButtons.vue";

// fix default icon problem
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
	iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
	iconUrl: require("leaflet/dist/images/marker-icon.png"),
	shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

export default {
	components: {
		MapButtons,
	},
	data() {
		return {
			osm_layer: L.tileLayer(
				"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
			),
			layercontrol: L.control.layers(),
			polygonMarkerOptions: {
				radius: 2,
				fillColor: "green",
				color: "green",
				weight: 2,
				opacity: 0.5,
				fillOpacity: 0.2,
			},
			pointMarkerOptions: {
				radius: 3,
				fillColor: "red",
				color: "red",
				weight: 3,
				opacity: 1,
				fillOpacity: 0.2,
			},
			drawControl: null,
			focusPoint: null,
			cancelSelectedPlekje: false,
			savingSelectedPlekje: false,
			userLocation: null,
		};
	},
	methods: {
		setupMap() {
			map = L.map("mapContainer", {
				center: [51.11, 4.46],
				crs: L.CRS.EPSG3857,
				zoom: 11,
				zoomControl: true,
				preferCanvas: false,
			});
			this.osm_layer.addTo(map);
			L.control.scale().addTo(map);
			this.layercontrol.addTo(map);
			this.layercontrol.addBaseLayer(this.osm_layer, "OSM layer");
		},
		addToolTip(feature, layer) {
			if (feature.properties && feature.properties.name) {
				layer.bindTooltip(feature.properties.name);
			}
		},
		displayFeatures(fc) {
			let fg;
			if (this.$store.state.editingPlekollection) {
				fg = this.drawnItems;
			} else {
				fg = L.featureGroup().addTo(map);
			}

			fc.features.forEach((f) => {
				switch (f.geometry.type) {
					case "Point":
						if (this.$store.state.editingPlekollection) {
							L.geoJSON(f, {
								onEachFeature: this.addToolTip,
							}).addTo(fg);
						} else {
							L.geoJSON(f, {
								pointToLayer: (feature, latlng) => {
									return L.circleMarker(
										latlng,
										this.pointMarkerOptions
									);
								},
								onEachFeature: this.addToolTip,
							}).addTo(fg);
						}
						break;
					case "Polygon":
					case "MultiPolygon":
						L.geoJSON(f, {
							style: this.polygonMarkerOptions,
							onEachFeature: this.addToolTip,
						}).addTo(fg);
						break;
					default:
						L.geoJSON(f, {
							onEachFeature: this.addToolTip,
						}).addTo(fg);
				}
			});
			if (!this.$store.state.editingPlekollection) {
				this.layercontrol.addOverlay(fg, fc.props.name);
			}
		},
		clearMapLayers() {
			map.eachLayer((layer) => {
				if (!layer._url) {
					map.removeLayer(layer);
					this.layercontrol.removeLayer(layer);
				}
			});
		},
		displayFeatureCollections() {
			this.clearMapLayers();
			this.$store.state.featureCollections.forEach((fc) => {
				this.displayFeatures(fc);
			});
		},
		addDrawControl(control_name) {
			if (!this.drawControl) {
				this.drawnItems = new L.FeatureGroup().addTo(map);
				this.layercontrol.addOverlay(this.drawnItems, control_name);
				this.drawControl = new L.Control.Draw({
					edit: {
						featureGroup: this.drawnItems,
						edit: false,
					},
					draw: {
						marker: true,
						polygon: true,
						polyline: true,
						circle: false,
						rectangle: false,
						circlemarker: true,
					},
				});
				this.drawControl.addTo(map);

				L.Draw.Polyline.prototype._onTouch = L.Util.falseFn;

				map.on("draw:created", (event) => {
					var layer = event.layer;
					layer.properties = {};
					var feature = (layer.feature = layer.feature || {});
					feature.type = "Feature";
					feature.properties = feature.properties || {};

					let marker_name = prompt("Name Plekje", " ");

					if (marker_name != null && marker_name != " ") {
						feature.properties["name"] = marker_name;

						layer.bindTooltip(feature.properties["name"]);

						this.drawnItems.addLayer(layer);

						if (this.$store.state.editingPlekollection) {
							this.$store.state.featureCollectionToEdit.features.push(
								layer.toGeoJSON()
							);
						}
					}
				});
			}
		},
		create() {
			api.createPlekollection(null, [], (pc) => {
				if (pc) {
					this.$store.state.selectedPlekollection = pc;
					api.setFeatureCollectionToEdit(() => {
						this.displayFeatureCollections();
						this.addDrawControl(
							this.$store.state.featureCollectionToEdit.props.name
						);
						this.displayFeatures(
							this.$store.state.featureCollectionToEdit
						);
					});
				}
			});
		},
		save() {
			if (this.$store.state.editingPlekollection) {
				api.editPlekollectionFlow(() => {
					this.displayFeatureCollections();
				});
			}

			//remove drawcontrol
			this.drawnItems = null;
			this.drawControl = null;
			this.layercontrol = L.control.layers();
			map.remove();
			this.setupMap();
			this.displayFeatureCollections();
		},
		edit() {
			this.$store.state.editingPlekollection = true;
			// nu edit ge gwn de eerste fc op map -> beter
			this.$store.state.featureCollectionToEdit =
				this.$store.state.featureCollections[0];
			this.$store.state.featureCollections = [];

			this.displayFeatureCollections();
			this.addDrawControl(
				this.$store.state.featureCollectionToEdit.props.name
			);
			this.displayFeatures(this.$store.state.featureCollectionToEdit);
		},
		savePlekje() {
			this.savingSelectedPlekje = true;
			let name = prompt("Name Plekje", "");
			if (name) {
				api.createPlekje(this.focusPoint.toGeoJSON(), name);
				map.removeLayer(this.focusPoint);
				this.focusPoint = null;
			}
		},
        saveHere(){
            let name = prompt("Name Plekje", "");
			if (name) {
				api.createPlekje(this.userLocation.toGeoJSON(), name);
			}
        },
		cancelSelectPlekje() {
			this.cancelSelectedPlekje = true;
			map.removeLayer(this.focusPoint);
			this.focusPoint = null;

            //map.removeLayer(this.userLocation);
			this.userLocation = null;
		},
		locateUser() {
			console.log("locating");
			if (navigator.geolocation) {
				navigator.geolocation.getCurrentPosition(
					this.showPosition,
					(e) => {
						console.log(e);
					}
				);
			} else {
				console.log("Geolocation is not supported by this browser.");
			}
		},
		showPosition(position) {
			if (!this.userLocation) {
				this.userLocation = L.circleMarker(
					[position.coords.latitude, position.coords.longitude],
					{
						radius: 15,
						color: "#17a2b8",
						fillOpacity: 0,
					}
				).addTo(map);
			} else {
				this.userLocation.setLatLng([
					position.coords.latitude,
					position.coords.longitude,
				]);
			}
            map.panTo(new L.LatLng(
					position.coords.latitude,
					position.coords.longitude,
				));
		},
	},
	mounted() {
		this.setupMap();

        //this.addDrawControl('new');

		if (this.$store.state.editingPlekollection) {
			this.addDrawControl(
				this.$store.state.featureCollectionToEdit.props.name
			);
			this.displayFeatures(this.$store.state.featureCollectionToEdit);
		} else {
			this.displayFeatureCollections();
		}

		map.on("click", (e) => {
			if (
				!this.$store.state.editingPlekollection &&
				!this.cancelSelectedPlekje &&
				!this.savingSelectedPlekje
			) {
				let location = e.latlng;
				if (!this.focusPoint) {
					this.focusPoint = L.circleMarker(location, {
						radius: 12,
						color: "#0d6efd",
						fillOpacity: 0,
					}).addTo(map);
				} else {
					this.focusPoint.setLatLng(location);
				}
			}
			this.cancelSelectedPlekje = false;
			this.savingSelectedPlekje = false;
		});
	},
	beforeUnmount() {
		if (map) {
			map.remove();
		}
	},
};
</script>

<style>
#mapContainer {
	width: 100vw;
	height: 90%;
	position: absolute;
}
</style>