<template>
  <Map></Map>
  <LoadUserData v-if="this.$store.state.userEmail"></LoadUserData>
</template>

<script>
import Map from "../components/Map.vue";
import LoadUserData from "../components/LoadUserData.vue"
export default {
   components: {
		Map,
        LoadUserData
	},
    beforeCreate() {
		if (this.$store.state.userIsAuthenticated&&!this.$store.state.userEmail) {
			this.$store.dispatch("auth0getUserEmail");
		}
	},
}
</script>

